.slider {
  min-width: @mobile-width;

  background-color: @lighten-color;
}

.slider__container {
  .flex-column(flex-start, center);
  width: @mobile-width;
  min-height: 705px;

  margin: 0 auto;
}

.slider__small-container {
  .flex-column(flex-start, center);
  width: 100%;
}

.slider__header {
  .base-font();
  align-self: flex-start;

  margin-top: 16px;
  margin-bottom: 23px;
  margin-left: 20px;

  font-size: 36px;
  line-height: 60px;
  color: @dark-color;
}

.slider__text {
  .second-font();
  align-self: flex-start;

  width: 266px;

  margin-top: 0;
  margin-bottom: 25px;
  margin-left: 20px;

  font-size: 14px;
  line-height: 18px;
  color: @gray-color;
}

.slider__total-table {
  .base-font();

  margin-top: 0;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;

  text-align: center;

  font-size: 14px;
  line-height: 20px;
  color: @dark-color;
  text-transform: uppercase;
}

.slider__total-row {
  .flex-row(flex-start, center);
}

.slider__total-caption {
  margin-left: 4px;
}

.slider__wrapper {
  margin-top: 5px;

  align-self: start;
}

.slider__img {
  width: 320px;
  height: 240px;

  margin-bottom: 30px;

  &_before {
    background-image: url("../img/before-mobile@1x.png");
    background-position: center;
    background-repeat: no-repeat;

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/before-mobile@2x.png");
      background-size: cover;
    }
  }

  &_after {
    background-image: url("../img/after-mobile@1x.png");
    background-position: center;
    background-repeat: no-repeat;

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/after-mobile@2x.png");
      background-size: cover;
    }
  }
}

.slider__button {
  .base-font();

  padding: 0;
  outline: none;
  border: none;
  box-shadow: 0 1px transparent;
  background-color: transparent;

  font-size: 16px;
  line-height: 22px;
  color: @dark-color;
  text-transform: uppercase;

  &_current {
    border-bottom: dashed 1px @button-color;
  }

  &:hover,
  &_focus {
    border: none;
    box-shadow: 0 1px @button-hover-color;
  }

  &:active {
    border: none;
    box-shadow: none;
  }
}

.slider__table tbody {
  .flex-row(space-between, stretch);
  flex-wrap: wrap;
  width: @wrapper-width;
}

.slider__row {
  .flex-row(space-between, stretch);
  width: 100%;
}

.slider__bottom-row {
  .flex-row(space-between, stretch);
  width: 100%;
  margin-bottom: 10px;
}

.slider__value-cell {
  .base-font();
  .flex-row(center, center);
  .outline(@dim-color);

  width: 124px;
  min-height: 56px;

  border-radius: 5px;

  font-size: 24px;
  line-height: 24px;
  color: @dark-color;
  text-transform: uppercase;

  text-align: center;
  vertical-align: middle;
}

.slider__value-cell span {
  margin: auto;
}

.slider__caption-cell {
  .second-font();
  .flex-row(center, center);
  width: 124px;

  text-align: center;

  transform: translateY(-8px);
}

.slider__caption-cell span {
  width: 87px;

  padding-left: 15px;
  padding-right: 15px;

  background-color: @lighten-color;

  font-size: 12px;
  line-height: 12px;
  color: @gray-color;
}

//------------------------------------------- TABLET -------------------------

@media (min-width: @tablet-width) {
  .slider {
    min-width: @tablet-width;
    position: relative;

    background-color: @light-color;

    &::before {
      .pseudo();
      bottom: 0;
      left: 0;

      width: 50%;
      height: 462px;

      background-color: @lighten-color;
    }

    &::after {
      .pseudo();
      bottom: 0;
      right: 0;

      width: 50%;
      height: 462px;

      background-color: @lighten-gray-color;
    }
  }

  .slider__container {
    width: @tablet-wrapper-width;
    min-height: 1080px;

    background-color: @light-color;
  }

  .slider__header {
    margin-top: 43px;
    margin-bottom: 65px;
    margin-left: 0;

    font-size: 60px;
  }

  .slider__text {
    width: auto;
    max-width: 100%;

    margin-bottom: 50px;
    margin-left: 0;
    padding-right: 30px;

    font-size: 16px;
    line-height: 24px;
  }

  .slider__table-wrapper {
    .flex-row(space-between, flex-start);
    width: @tablet-wrapper-width;
  }

  .slider__table tbody {
    width: 365px;
  }

  .slider__value-cell {
    width: 162px;
    min-height: 72px;

    font-size: 30px;
    line-height: 37px;
  }

  .slider__caption-cell {
    width: 162px;
  }

  .slider__caption-cell span {
    width: 144px;

    padding-left: 8px;
    padding-right: 8px;

    background-color: @light-color;

    font-size: 14px;
    line-height: 14px;
  }

  .slider__total-table {
    width: 200px;

    margin-top: 8px;
    margin-right: 0;
    padding-left: 25px;

    text-align: right;

    font-size: 20px;
    line-height: 30px;
  }

  .slider__total-row {
    .flex-column(flex-start, flex-end);
  }

  .slider__total-caption {
    margin-left: 4px;
  }

  .slider__wrapper {
    .flex-row(center, flex-start);
    flex-wrap: wrap;
    width: @tablet-wrapper-width;

    position: relative;

    margin-top: 0;
    padding-bottom: 50px;

    align-self: flex-start;

    z-index: 1;
  }

  .slider__img {
    height: 517px;

    margin-top: 60px;
    margin-bottom: 15px;


    align-self: center;

    &_before {
      width: 345px;

      background-image: url("../img/before-tablet@1x.png");
      background-position: top left 5px;
      background-repeat: no-repeat;

      @media (min-resolution: @retina-dppx) {
        background-image: url("../img/before-tablet@2x.png");
        background-size: cover;
      }
    }

    &_after {
      width: 345px;

      background-image: url("../img/after-tablet@1x.png");
      background-position: top right -5px;
      background-repeat: no-repeat;

      @media (min-resolution: @retina-dppx) {
        background-image: url("../img/after-tablet@2x.png");
        background-size: cover;
      }
    }
  }

  .slider__button {
    font-size: 20px;
    line-height: 32px;
  }
}

//------------------------------------------- DESKTOP -------------------------


@media (min-width: @desktop-width) {
  .slider {
    min-width: @desktop-width;

    &::before {
      right: calc(50% - 324px);

      width: calc(50% + 324px);
    }

    &::after {
      left: calc(50% + 324px);

      width: calc(50% - 324px);
    }
  }

  .slider__container {
    .flex-row(space-between, flex-start);
    flex-wrap: wrap;
    width: @desktop-wrapper-width;
    min-height: 650px;
  }

  .slider__small-container {
    .flex-column(flex-start, flex-start);
    width: auto;

    z-index: 1;
  }

  .slider__text {
    width: @desktop-column-width;

    margin-bottom: 67px;
    margin-right: 50px;
    padding-right: 0;
  }

  .slider__table-wrapper {
    .flex-column(flex-start, flex-start);
    width: @desktop-column-width;
  }

  .slider__caption-cell span {
    background-color: @lighten-color;
  }

  .slider__total-table {
    width: auto;

    margin-top: 30px;
    margin-left: 0;

    padding-left: 0;
  }

  .slider__total-row {
    .flex-row(flex-start, center);
  }

  .slider__total-caption {
    margin-left: 65px;
  }

  .slider__wrapper {
    width: 670px;
  }

  .slider__header {
    width: @desktop-column-width;

    margin-top: 54px;
    margin-bottom: 145px;
  }

  .slider__img {
    margin-top: 0;
    margin-bottom: 33px;

    align-self: flex-start;

    &_before {
      width: 365px;

      background-image: url("../img/before-desktop@1x.png");
      background-position: top left;
      background-repeat: no-repeat;

      @media (min-resolution: @retina-dppx) {
        background-image: url("../img/before-desktop@2x.png");
        background-size: cover;
      }
    }

    &_after {
      width: 261px;

      background-image: url("../img/after-desktop@1x.png");
      background-position: top right -65px;
      background-repeat: no-repeat;

      @media (min-resolution: @retina-dppx) {
        background-image: url("../img/after-desktop@2x.png");
        background-size: cover;
      }
    }
  }

  .slider__button {
    padding: 0;
  }
}
