.button {
  .flex-row(center, center);
  border: none;

  background-color: @leaf-color;

  &:hover,
  &:focus {
    outline: none;
    background-color: @grass-color;
  }

  &_gray {
    background-color: @lighten-color;

    &:hover,
    &:focus {
      background-color: @button-gray-color;
    }
  }

  &:active p {
    opacity: 0.3;
  }
}

.button__caption {
  .base-font();

  font-size: 14px;
  text-transform: uppercase;
  color: @light-color;
  letter-spacing: 1px;

  &_gray {
    color: @gray-color;
  }
}
