.header {
  .base-font();
  min-width: @mobile-width;
}

.header__container {
  .flex-row(flex-start, flex-start);
  width: 100%;
  min-height: 65px;

  position: relative;
}

.header__logo {
  margin-top: 10px;
  margin-left: 20px;

  width: 190px;
  height: 40px;

  &:hover,
  &:focus {
    outline: none;
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.header__button {
  position: absolute;
  top: 10px;
  right: 10px;

  width: 1px;
  height: 1px;

  font-size: 0;

  border: none;
  outline: none;
  padding: 0;

  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
}

.header__button_close {
  top: 23px;
  right: 22px;

  width: 19px;
  height: 19px;

  background-image: url("../img/icon-close.svg");
}

.header__button_open {
  top: 23px;
  right: 19px;

  width: 25px;
  height: 19px;

  background-image: url("../img/icon-mobile-menu.svg");
}

.header__nav {
  width: 100%;
}

.header__menu {
  .list-reset();
  min-width: @mobile-width;
}

.header__hide-menu {
  display: none;
}

.header__menu-item {
  .flex-column(center, center);
  width: 100%;
  max-width: 100%;
  min-height: 66px;

  position: relative;

  letter-spacing: 0.5px;
  text-transform: uppercase;

  &:first-child::before {
    .dim-line(0, 100%);
    top: 0;
  }

  &::after {
    .dim-line(0, 100%);
    bottom: 0;
  }
}

.header__link {
  .flex-column(center, center);
  font-size: 20px;
  color: @dark-color;
  line-height: 20px;
}

//------------------------------------------- TABLET -------------------------
@media (min-width: @tablet-width) {
  .header {
    .flex-row(space-between, center);
    width: @tablet-width;

    margin-top: 22px;
    margin-bottom: 30px;
    margin-left: auto;
    margin-right: auto;

    overflow-x: auto;
  }

  .header__container {
    width: auto;
    height: auto;
    position: static;
    margin-left: 30px;
    margin-right: 0;
  }

  .header__logo {
    width: 202px;
    height: 59px;

    margin-top: 0;
    margin-left: 0;
  }

  .header__logo img {
    width: 202px;
    height: 59px;
  }

  .header__button {
    display: none;
  }

  .header__nav {
    .flex-column(flex-start, center);
    width: auto;
    margin: 0;
  }

  .header__menu {
    .flex-row(flex-end, center);
    max-width: 500px;

    margin-right: 30px;
    margin-left: 30px;
  }

  .header__hide-menu {
    display: flex;
  }

  .header__menu-item {
    width: auto;
    height: auto;

    margin-left: 36px;

    letter-spacing: 0;

    &:first-child {
      margin-left: 0;
    }

    &:first-child::before {
      display: none;
    }

    &::after {
      display: none;
    }
  }

  .header__link {
    font-size: 18px;
    line-height: 40px;
    color: @darken-color;

    &:hover,
    &:focus {
      outline: none;
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }

  .header__link p {
    margin-top: 0;
    margin-bottom: 0;
  }

  .header__link_index-current,
  .header__link_current {
    border-bottom: 2px solid @leaf-color;
  }
}

//------------------------------------------- DESKTOP -------------------------

@media (min-width: @desktop-width) {
  .header {
    .flex-row(center, stretch);
    position: static;
    width: auto;

    margin-top: 0;
    margin-bottom: 80px;
    margin-left: 0;
    margin-right: 0;
  }

  .header__container {
    flex-grow: 1;

    position: relative;

    width: calc(@calc);
    min-height: 100px;

    background-color: @light-color;

    margin: 0;

    &_index {
      min-height: @desktop-header-height;
    }
  }

  .header__logo {
    position: absolute;
    top: 53px;
    right: 407px;
    left: auto;

    width: 203px;
    height: 60px;

    margin: 0;
  }

  .header__logo img {
    width: 203px;
    height: 60px;
  }

  .header__nav {
    .flex-row(flex-start, flex-start);
    position: static;
    flex-grow: 1;
    display: flex;
    width: calc(@calc);

    margin-top: 0;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;

    &_index {
      min-height: @desktop-header-height;

      background-color: @leaf-color;
      background-image: url("../img/index-background-desktop@1x.jpg");
      background-size: auto 100%;
      background-position: 0 0;
      background-repeat: no-repeat;

      @media (min-resolution: @retina-dppx) {
        background-image: url("../img/index-background-desktop@2x.jpg");
        background-size: auto 100%;
      }
    }

    &_catalog {
      min-height: 120px;
    }
  }

  .header__menu {
    .flex-row(flex-start, center);
    position: static;
    flex-wrap: wrap;

    max-width: calc(@calc);

    margin-top: 66px;
    margin-right: 0;
    margin-left: 0;
    padding-left: 130px;

    z-index: 2;

    &_index {
      margin-top: 53px;
    }
  }

  .header__menu-item {
    position: static;
    margin-left: 35px;

    &:first-child {
      margin-left: auto;
    }

    &:last-child {
      margin-right: auto;
    }

    &_catalog {
      min-height: 42px;
    }
  }

  .header__link {
    font-size: 20px;

    &_index {
      color: @light-color;
    }

    &_index-current {
      color: @light-color;
      border-bottom: 2px solid @light-color;
    }

    &_current {
      border-bottom: 2px solid @leaf-color;
    }
  }
}
