    margin-right: 21px;
  }
}
.form {
  min-width: 320px;
}
.form__header {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  align-self: flex-start;
  margin-top: 15px;
  margin-bottom: 25px;
  margin-left: 20px;
  font-size: 36px;
  line-height: 60px;
  color: #111111;
}
.form__wide-fieldset {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  margin: 0;
  padding: 0;
  border: none;
}
.form__radio-wrapper {
  box-shadow: inset 0 0 0 1px #ededed;
  flex-grow: 1;
  width: auto;
  max-width: 630px;
  min-height: 158px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 27px;
  padding-bottom: 0;
  padding-left: 46px;
  border: none;
}
.form__checkbox-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 26px;
  padding-left: 20px;
}
.form__text {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  width: 230px;
  margin-top: 0;
  margin-bottom: 40px;
  margin-left: 20px;
  font-size: 14px;
  line-height: 18px;
  color: #111111;
  text-transform: uppercase;
}
.form__label {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  position: relative;
  width: 87.5%;
  margin-bottom: 14px;
  margin-left: 20px;
  margin-right: 20px;
  font-size: 16px;
  line-height: 18px;
}
.form__label_radio {
  width: 200px;
  position: relative;
  margin-bottom: 25px;
  margin-right: 0;
}
.form__label_radio::before {
  content: "";
  position: absolute;
  box-shadow: inset 0 0 0 1px #cdcdcd;
  top: -6px;
  left: -46px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.form__label_radio:hover::before {
  box-shadow: inset 0 0 0 1px #bababa;
}
.form__label_radio:active {
  opacity: 0.3;
}
.form__radio:focus + .form__label_radio::before {
  box-shadow: inset 0 0 0 1px #bababa;
}
.form__radio:checked + .form__label_radio::after {
  content: "";
  position: absolute;
  box-shadow: inset 0 0 0 1px #cdcdcd;
  top: 3px;
  left: -37px;
  width: 12px;
  height: 12px;
  background-color: #68b738;
  border-radius: 50%;
}
.form__label_checkbox {
  width: 200px;
  position: relative;
  margin-top: 10px;
  margin-left: 45px;
  margin-right: 0;
}
.form__label_checkbox::before {
  content: "";
  position: absolute;
  box-shadow: inset 0 0 0 1px #cdcdcd;
  top: -6px;
  left: -45px;
  width: 30px;
  height: 30px;
}
.form__label_checkbox:hover::before {
  box-shadow: inset 0 0 0 1px #bababa;
}
.form__checkbox:focus + .form__label_checkbox::before {
  box-shadow: inset 0 0 0 1px #bababa;
}
.form__checkbox:checked + .form__label_checkbox::after {
  content: "";
  position: absolute;
  top: -1px;
  left: -40px;
  width: 22px;
  height: 17px;
  background-image: url("../img/icon-tick.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.form__label_checkbox:active {
  opacity: 0.3;
}
.form__caption {
  flex-shrink: 0;
  padding-right: 15px;
  color: #222222;
  text-transform: uppercase;
}
.form__caption_padding {
  padding-right: 10px;
}
.form__topic {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-top: 29px;
  margin-bottom: 32px;
  font-size: 24px;
  line-height: 24px;
  color: #111111;
}
.form__topic::before {
  content: "";
  position: absolute;
  top: 13px;
  right: 0;
  width: 100%;
  height: 3px;
  background-color: #68b738;
  z-index: -1;
}
.form__topic span {
  max-width: 220px;
  padding-left: 20px;
  padding-right: 18px;
  background-color: #ffffff;
}
.form__icon {
  content: "";
  position: absolute;
  fill: #222222;
}
.form__icon_mail {
  top: calc(50% - 7px);
  right: 15px;
}
.form__icon_phone {
  top: calc(50% - 11px);
  right: 13px;
}
.form__input {
  box-shadow: inset 0 0 0 1px rgba(205, 205, 205, 0.5);
  outline: none;
  flex-basis: 20px;
  flex-grow: 1;
  max-width: 570px;
  height: 45px;
  padding-left: 16px;
  border: none;
  color: #444444;
  text-transform: uppercase;
}
.form__input__email,
.form__input_tel {
  padding-right: 40px;
}
.form__input:hover {
  box-shadow: inset 0 0 0 1px #cdcdcd;
}
.form__input:focus,
.form__input:active {
  box-shadow: inset 0 0 0 2px #444444;
}
.form__input:focus + .form__icon,
.form__input:active + .form__icon {
  fill: #68b738;
}
.form__input:invalid {
  box-shadow: inset 0 0 0 2px #ff8282;
}
.form__input:invalid + .form__icon {
  fill: #ff8282;
}
.form__textarea {
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  width: 280px;
  height: 150px;
  overflow: auto;
  resize: none;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 15px;
  padding-left: 15px;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-transform: uppercase;
  border: none;
}
.form__hint {
  font-family: "Arial", sans-serif;
  font-weight: 400;
  align-self: center;
  text-align: center;
  margin-top: 22px;
  margin-bottom: 33px;
  font-size: 14px;
  line-height: 16px;
  color: #444444;
}
.form__button {
  width: 280px;
  height: 40px;
  margin: auto;
}
.form__button-caption {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 16px;
  text-transform: uppercase;
  color: #ffffff;
}
.form__label-wrapper {
  width: 100%;
}
.form__button-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}
