.toggle {
  .flex-row(center, center);
  width: @wrapper-width;

  margin: 0 auto;
}

.toggle__toggle {
  .outline(@light-gray-color);
  position: relative;

  width: 84px;
  height: 14px;

  margin-left: 19px;
  margin-right: 19px;

  background-color: @light-color;

  border-radius: 6px;
}

.toggle__control {
  position: absolute;
  top: calc(50% - 3px);
  left: 6px;

  width: 35px;
  height: 6px;

  background-color: @leaf-color;

  border-radius: 3px;
}

//------------------------------------------- TABLET -------------------------

@media (min-width: @tablet-width) {
  .toggle {
    width: 100%;

    margin: 0 auto;
  }

  .toggle__toggle {
    width: 428px;
    height: 6px;

    margin-left: 22px;
    margin-right: 22px;

    background-color: @toggle-color;
    box-shadow: none;
  }

  .toggle__control {
    top: calc(50% - 17px);
    left: calc(50% - 15px);

    width: 34px;
    height: 34px;

    background-color: @light-color;
    border-radius: 50%;
    box-shadow: inset 0 0 0 1px @light-gray-color,
      0 2px 5px 0 rgba(0, 0, 0, 0.15);

    &::after {
      .pseudo();
      top: calc(50% - 4px);
      left: calc(50% - 4px);

      width: 8px;
      height: 8px;

      background-color: @leaf-color;
      border-radius: 50%;
    }

    &:hover {
      box-shadow: inset 0 0 0 1px @light-gray-color,
        0 3px 5px 0 rgba(0, 0, 0, 0.25);
    }

    &:active {

      box-shadow: inset 0 0 0 2px @leaf-color,
        0 4px 5px 0 rgba(0, 0, 0, 0.3);
    }
  }
}

//------------------------------------------- DESKTOP -------------------------

@media (min-width: @desktop-width) {
  .toggle {
    padding-left: 100px;
  }

  .toggle__toggle {
    margin-left: 24px;
    margin-right: 21px;
  }
}
