.form {
  min-width: @mobile-width;
}

.form__header {
  .base-font();
  align-self: flex-start;

  margin-top: 15px;
  margin-bottom: 25px;
  margin-left: 20px;

  font-size: 36px;
  line-height: 60px;
  color: @dark-color;
}

.form__wide-fieldset {
  .flex-column(flex-start, flex-start);
  width: 100%;

  margin: 0;
  padding: 0;

  border: none;
}

.form__radio-wrapper {
  .outline(@smooth-color);
  flex-grow: 1;
  width: auto;
  max-width: @desktop-form-width;

  min-height: 158px;

  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 27px;
  padding-bottom: 0;
  padding-left: 46px;

  border: none;
}

.form__checkbox-wrapper {
  .flex-column(flex-start, flex-start);
  margin-bottom: 26px;
  padding-left: 20px;
}

.form__text {
  .base-font();
  width: 230px;

  margin-top: 0;
  margin-bottom: 40px;
  margin-left: 20px;

  font-size: 14px;
  line-height: 18px;
  color: @dark-color;
  text-transform: uppercase;
}

.form__label {
  .flex-row(flex-start, center);
  .base-font();
  position: relative;
  width: 87.5%;

  margin-bottom: 14px;
  margin-left: 20px;
  margin-right: 20px;

  font-size: 16px;
  line-height: 18px;

  &_radio {
    width: 200px;

    position: relative;

    margin-bottom: 25px;
    margin-right: 0;

    &::before {
      .pseudo();
      .outline(@light-gray-color);
      top: -6px;
      left: -46px;

      width: 30px;
      height: 30px;

      border-radius: 50%;
    }

    &:hover::before {
      .outline(@radio-color);
    }

    &:active {
      opacity: 0.3;
    }

    .form__radio:focus + &::before {
      .outline(@radio-color);
    }

    .form__radio:checked + &::after {
      .pseudo();
      .outline(@light-gray-color);
      top: 3px;
      left: -37px;

      width: 12px;
      height: 12px;

      background-color: @leaf-color;
      border-radius: 50%;
    }
  }

  &_checkbox {
    width: 200px;

    position: relative;

    margin-top: 10px;
    margin-left: 45px;
    margin-right: 0;

    &::before {
      .pseudo();
      .outline(@light-gray-color);
      top: -6px;
      left: -45px;

      width: 30px;
      height: 30px;
    }

    &:hover::before {
      .outline(@radio-color);
    }

    .form__checkbox:focus + &::before {
      .outline(@radio-color);
    }

    .form__checkbox:checked + &::after {
      .pseudo();
      top: -1px;
      left: -40px;

      width: 22px;
      height: 17px;

      background-image: url("../img/icon-tick.svg");
      background-position: center;
      background-repeat: no-repeat;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.form__caption {
  flex-shrink: 0;

  padding-right: 15px;

  color: @darken-color;
  text-transform: uppercase;

  &_padding {
    padding-right: 10px;
  }
}

.form__topic {
  .base-font();
  .flex-row(flex-start, center);
  position: relative;

  margin-top: 29px;
  margin-bottom: 32px;

  font-size: 24px;
  line-height: 24px;
  color: @dark-color;

  &::before {
    .pseudo();
    top: 13px;
    right: 0;

    width: 100%;
    height: 3px;

    background-color: @leaf-color;
    z-index: -1;
  }

  span {
    max-width: 220px;

    padding-left: 20px;
    padding-right: 18px;

    background-color: @light-color;
  }
}

.form__icon {
  .pseudo();

  fill: @darken-color;

  &_mail {
    top: calc(50% - 7px);
    right: 15px;
  }

  &_phone {
    top: calc(50% - 11px);
    right: 13px;
  }
}

.form__input {
  .outline(@input-color);
  outline: none;

  flex-basis: 20px;
  flex-grow: 1;

  max-width: 570px;
  height: 45px;

  padding-left: 16px;

  border: none;

  color: @gray-color;
  text-transform: uppercase;

  &__email,
  &_tel {
    padding-right: 40px;
  }

  &:hover {
    .outline(@light-gray-color);
  }

  &:focus,
  &:active {
    box-shadow: inset 0 0 0 2px @gray-color;
  }

  &:focus + .form__icon,
  &:active + .form__icon {
    fill: @leaf-color;
  }

  &:invalid {
    box-shadow: inset 0 0 0 2px @error-color;
  }

  &:invalid + .form__icon {
    fill: @error-color;
  }
}

.form__textarea {
  .outline(@dim-color);
  .base-font();
  width: @wrapper-width;
  height: 150px;

  overflow: auto;
  resize: none;

  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 15px;
  padding-left: 15px;

  font-size: 16px;
  line-height: 18px;
  color: @gray-color;
  text-transform: uppercase;
  border: none;
}

.form__hint {
  .second-font();
  align-self: center;

  text-align: center;

  margin-top: 22px;
  margin-bottom: 33px;

  font-size: 14px;
  line-height: 16px;
  color: @gray-color;
}

.form__button {
  width: @wrapper-width;
  height: 40px;

  margin: auto;
}

.form__button-caption {
  .base-font();
  margin-top: 0;
  margin-bottom: 0;

  font-size: 16px;
  text-transform: uppercase;
  color: @light-color;
}

.form__label-wrapper {
  width: 100%;
}

.form__button-wrapper {
  .flex-column(flex-start, center);
  width: 100%;
}

//------------------------------------------- TABLET -------------------------

@media (min-width: @tablet-width) {
  .form {
    min-width: @tablet-width;
  }

  .form__header {
    margin-top: 23px;
    margin-bottom: 55px;
    margin-left: 30px;

    font-size: 60px;
  }

  .form__text {
    width: 350px;

    margin-bottom: 65px;
    margin-left: 70px;

    font-size: 20px;
    line-height: 30px;
  }

  .form__icon {
    &_mail {
      right: 18px;
    }

    &_phone {
      right: 15px;
    }
  }

  .form__label {
    .flex-row(flex-start, center);
    width: @desktop-form-width;

    margin-bottom: 20px;
    margin-left: 70px;

    font-size: 20px;
    line-height: 20px;

    &_radio {
      width: 100%;

      margin-bottom: 33px;

      &::before {
        top: -10px;
        left: -57px;

        width: 40px;
        height: 40px;
      }
    }

    .form__radio:checked + &::after {
      top: 1px;
      left: -46px;

      width: 18px;
      height: 18px;
    }

    &_checkbox {
      width: 265px;
      margin-bottom: 44px;
      margin-left: 59px;
      margin-right: 20px;

      &::before {
        top: -8px;
        left: -60px;

        width: 40px;
        height: 40px;
      }

      .form__checkbox:checked + &::after {
        top: 3px;
        left: -50px;
      }
    }
  }

  .form__caption {
    padding-right: 16px;
  }

  .form__input {
    max-width: 567px;
    height: 52px;

    padding-left: 20px;
  }

  .form__radio-wrapper {
    flex-grow: 0;
    width: @desktop-form-width;
    max-width: @desktop-form-width;
    box-sizing: border-box;

    margin-top: 52px;
    margin-bottom: 12px;
    padding-top: 35px;
    padding-left: 18px;
  }

  .form__topic {
    width: 100%;

    margin-top: 65px;
    margin-bottom: 76px;

    font-size: 36px;
    line-height: 36px;

    &::before {
      top: 23px;
    }

    span {
      max-width: @desktop-form-width;

      margin-left: 31px;
      padding-left: 38px;
      padding-right: 40px;
    }
  }

  .form__textarea {
    width: @desktop-form-width;
    height: 174px;
    padding-top: 10px;
    padding-left: 20px;

    font-size: 20px;
    line-height: 30px;
  }

  .form__checkbox-wrapper {
    .flex-row(flex-start, stretch);
    flex-wrap: wrap;

    width: 100%;

    margin-top: -20px;
    margin-bottom: 20px;
    padding-left: 70px;
  }

  .form__button-wrapper {
    .flex-row(flex-start, stretch);

    margin-bottom: 72px;
    padding-left: 70px;
  }

  .form__button {
    width: 283px;
    height: 52px;

    margin: 0;
  }

  .form__button-caption {
    font-size: 20px;
  }

  .form__hint {
    margin-top: 12px;
    margin-bottom: 15px;
    margin-left: 61px;

    font-size: 16px;
  }
}

//------------------------------------------- DESKTOP -------------------------

@media (min-width: @desktop-width) {
  .form {
    min-width: @desktop-width;
  }

  .form__form {
    .flex-row(center, stretch);
    flex-wrap: wrap;
    width: 100%;
  }

  .form__wide-fieldset_half {
    .flex-column(flex-start, flex-end);
    width: @desktop-size-width;
    max-width: @desktop-size-width;

    margin-left: 0;
    margin-right: 40px;
  }

  .form__radio-wrapper {
    width: @desktop-size-width;
    max-width: @desktop-size-width;
    height: 196px;

    margin-top: auto;
    margin-bottom: 20px;
    margin-left: 40px;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
  }

  .form__header {
    width: @desktop-size-width;

    margin-top: -10px;
    margin-left: auto;
    margin-right: 0;

    text-align: left;
  }

  .form__text {
    width: 350px;

    margin-left: auto;
    margin-right: 220px;
  }

  .form__label-wrapper {
    .flex-row(center, flex-start);
  }

  .form__label {
    width: 100%;

    margin-left: 0;
    margin-right: 0;

    &_radio {
      margin-left: 88px;
    }

    &_row {
      width: @desktop-size-width;

      margin-bottom: 15px;
      margin-left: 40px;
      margin-right: 40px;
    }

    &_checkbox {
      width: 185px;

      margin-left: 60px;
    }
  }

  .form__topic {
    .flex-row(flex-start, center);
    margin-top: 50px;

    span {
      max-width: @desktop-form-width;

      margin-left: calc(50% - 650px);
      padding-left: 40px;
      padding-right: 40px;
    }
  }

  .form__input {
    max-width: 506px;

    &_email {
      background-position: right 20px center;
    }

    &_tel {
      background-position: right 18px center;
    }
  }

  .form__textarea {
    width: @desktop-wrapper-width;

    margin-bottom: 67px;
  }

  .form__checkbox-wrapper {
    .flex-row(space-between, stretch);

    width: @desktop-wrapper-width;

    margin-right: auto;
    margin-left: auto;
    margin-bottom: 30px;
    padding-left: 0;
  }

  .form__button-wrapper {
    width: @desktop-wrapper-width;

    margin-right: auto;
    margin-left: auto;
    margin-bottom: 82px;
    padding-left: 0;
  }

  .form__button {
    width: 568px;
  }

  .form__hint {
    margin-left: 83px;
  }
}
