.social {
  .list-reset();
  .flex-row (center, center);
}

.social__item {
  .flex-row (center, center);
}

.social__link {
  .flex-row (center, center);
  outline: none;
  font-size: 0;

  fill: @brownish-grey-color;

  &:hover .social__svg,
  &:focus .social__svg {
    fill: @leaf-color;
  }

  &:active .social__svg {
    fill: @leaf-color;
    fill-opacity: 0.3;
  }
}

//------------------------------------------- TABLET -------------------------

@media (min-width: @tablet-width) {
  .social__svg {
    &_vk {
      width: 30px;
      height: 16px;
    }

    &_insta {
      width: 23px;
      height: 23px;
    }

    &_fb {
      width: 14px;
      height: 28px;
    }
  }
}
