.features-list {
  min-width: @mobile-width;
}

.features-list__container {
  width: @mobile-width;
  margin: 0 auto;
}

.features-list__list {
  .list-reset();
  .flex-column (flex-start, center);
  width: 100%;
}

.features-list__item {
  .flex-column (flex-start, flex-start);
  width: @wrapper-width;
  min-height: 266px;

  margin-bottom: 20px;
  padding-bottom: 18px;

  background-color: @lighten-color;

  &:last-child {
    margin-bottom: 0;
  }
}

.features-list__caption {
  .base-font();
  position: relative;

  margin-top: 30px;
  margin-bottom: 30px;
  margin-left: 92px;

  color: @dark-color;
  font-size: 24px;
  text-transform: uppercase;

  &::before {
    .pseudo();
    width: 50px;
    height: 50px;

    border-radius: 50%;

    top: -5px;
    left: -70px;

    background-color: @leaf-color;
  }

  &_losing-weight::after {
    .pseudo();
    width: 35px;
    height: 50px;

    top: -8px;
    left: -62px;

    background-image: url("../img/icon-cat-slim.svg");
    background-position: 0 0;
    background-repeat: no-repeat;
  }

  &_getting-weight::after {
    .pseudo();
    width: 67px;
    height: 28px;

    top: 3px;
    left: -79px;

    background-image: url("../img/icon-cat-pro.svg");
    background-position: 0 0;
    background-repeat: no-repeat;
  }
}

.features-list__text {
  .second-font();
  width: 227px;

  position: relative;

  margin-top: 0;
  margin-bottom: 40px;
  margin-left: 20px;

  color: @gray-color;
  font-size: 14px;
  line-height: 18px;

  &::before {
    .pseudo();
    bottom: -23px;
    left: 0;

    width: 240px;
    height: 1px;

    background-color: @dim-color;
  }
}

.features-list__link {
  .base-font();
  position: relative;

  margin-left: 20px;
  padding-right: 70px;

  color: @dark-color;
  font-size: 16px;
  text-transform: uppercase;

  background-image: url("../img/icon-right-arrow-long.svg");
  background-repeat: no-repeat;
  background-position: top 50% right 35px;
  background-size: 25px 12px;

  &:hover,
  &:focus {
    outline: none;

    background-image: url("../img/icon-right-arrow-verylong.svg");
    background-position: top 50% right 25px;
    background-size: 34px 12px;
  }

  &:active {
    opacity: 0.3;
  }
}

//------------------------------------------- TABLET -------------------------

@media (min-width: @tablet-width) {
  .features-list {
    min-width: @tablet-width;
    margin: 0 auto;
  }

  .features-list__container {
    width: @tablet-wrapper-width;
    margin: 0 auto;
  }

  .features-list__item {
    width: @tablet-wrapper-width;
    min-height: 372px;

    margin-bottom: 30px;
    padding-bottom: 55px;
    padding-left: 50px;
  }

  .features-list__caption {
    margin-top: 67px;
    margin-bottom: 17px;
    margin-left: 3px;

    font-size: 36px;

    &::before {
      width: 200px;
      height: 200px;

      top: 25px;
      left: 394px;
    }

    &_losing-weight::after {
      width: 141px;
      height: 197px;

      top: 11px;
      left: 422px;
    }

    &_getting-weight::after {
      width: 268px;
      height: 113px;

      top: 67px;
      left: 360px;
    }
  }

  .features-list__text {
    width: 372px;

    margin-bottom: 50px;
    margin-left: 3px;

    font-size: 16px;
    line-height: 24px;

    &_340 {
      width: 340px;
    }

    &::before {
      display: none;
    }
  }

  .features-list__link {
    margin-left: 3px;
    padding-right: 83px;

    font-size: 20px;

    &:hover,
    &:focus {
      outline: none;
    }
  }
}

//------------------------------------------- DESKTOP -------------------------

@media (min-width: @desktop-width) {
  .features-list {
    min-width: @desktop-width;
  }

  .features-list__container {
    width: @desktop-wrapper-width;
    margin: 0 auto;
  }

  .features-list__list {
    .flex-row (center, center);
    flex-wrap: wrap;
  }

  .features-list__item {
    width: 570px;

    margin-right: 80px;

    &:nth-child(2n) {
      margin-right: 0;
    }

    &:last-child {
      margin-bottom: 30px;
    }
  }

  .features-list__caption {
    margin-top: 68px;
    margin-bottom: 64px;
    margin-left: 163px;

    &::before {
      width: 100px;
      height: 100px;

      top: -20px;
      left: -160px;
    }

    &_losing-weight::after {
      width: 71px;
      height: 97px;

      top: -27px;
      left: -147px;
    }

    &_getting-weight::after {
      width: 134px;
      height: 57px;

      top: 0;
      left: -179px;
    }
  }

  .features-list__text {
    width: 470px;

    margin-bottom: 28px;
  }
}
