@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
  margin: 0;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;

  @media (min-width: @tablet-width) {
    overflow-x: auto;
  }
}

body {
  .base-font();
  margin: 0;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;

  @media (min-width: @tablet-width) {
    overflow-x: auto;
  }
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
