.bonus {
  .flex-column (center, center);
  width: @wrapper-width;
  min-height: 289px;

  margin-top: 39px;
  margin-bottom: 35px;
  margin-left: auto;
  margin-right: auto;

  background-color: @leaf-color;
  background-image: url("../img/gift-photo-mobile@1x.jpg");
  background-position: center;
  background-repeat: no-repeat;

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");
    background-size: cover;
  }
}

.bonus__text {
  .second-font();
  width: 165px;
  position: relative;

  margin-top: 160px;

  text-align: center;

  font-size: 16px;
  line-height: 20px;
  color: @light-color;

  &::after {
    .pseudo();
    top: -122px;
    left: calc(50% - 27px);

    width: 56px;
    height: 61px;

    background-image: url("../img/icon-gift.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}

//------------------------------------------- TABLET -------------------------

@media (min-width: @tablet-width) {
  .bonus {
    .flex-column(flex-start, flex-start);
    width: 100%;
    min-height: 200px;

    position: relative;

    margin-top: 69px;
    margin-bottom: 76px;
    padding-top: 53px;
    padding-bottom: 10px;
    padding-left: 424px;

    background-image: url("../img/gift-photo-tablet@1x.jpg");

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
      background-size: cover;
    }

    &::after {
      .pseudo();
      top: 65px;
      left: 91px;

      width: 56px;
      height: 61px;

      background-image: url("../img/icon-gift.svg");
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  .bonus__text {
    position: static;

    margin-top: 19px;

    text-align: start;

    &::after {
      display: none;
    }
  }
}

//------------------------------------------- DESKTOP -------------------------

@media (min-width: @desktop-width) {
  .bonus {
    .flex-column (center, center);
    width: @desktop-item-width;
    min-height: 289px;

    position: static;

    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: 0;
    padding-top: 0;
    padding-left: 0;

    background-color: @leaf-color;
    background-image: url("../img/gift-photo-desktop@1x.jpg");
    background-position: center;
    background-repeat: no-repeat;

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
      background-size: cover;
    }


    &::after {
      display: none;
    }
  }

  .bonus__text {
    position: relative;

    margin-top: 160px;
    margin-bottom: 0;

    text-align: center;

    &::after {
      display: block;
    }
  }
}
