.catalog-second {
  min-width: @mobile-width;
}

.catalog-second__container {
  .flex-column(flex-start, center);

  position: relative;

  &::before {
    .pseudo();
    width: 100%;
    height: 3px;

    top: 42px;
    right: 0;

    background-color: @leaf-color;
  }
}

.catalog-second__header {
  .base-font();
  align-self: start;
  position: relative;

  width: 269px;

  margin-top: 30px;
  margin-bottom: 33px;
  padding-left: 19px;
  padding-right: 24px;

  font-size: 24px;
  color: @dark-color;
  line-height: 24px;
  background-color: @light-color;
}

.catalog-second__list {
  .list-reset();
}

.catalog-second__item {
  .flex-column(flex-start, center);
  width: @wrapper-width;

  position: relative;

  margin: 0 auto;
  padding-bottom: 19px;

  &:first-child::before {
    .dim-line(0, @wrapper-width);
    top: 0;
  }

  &::after {
    .dim-line(0, @wrapper-width);
    bottom: 0;
  }
}

.catalog-second__link {
  .flex-column(center, flex-start);
  font-size: 0;
  width: @wrapper-width;
}

.catalog-second__caption {
  .base-font();
  margin-top: 13px;
  margin-bottom: 15px;
  margin-left: 2px;

  font-size: 16px;
  text-transform: uppercase;
  line-height: 16px;
  color: @darken-color;
}

.catalog-second__table {
  .second-font();
  width: 100%;

  margin-bottom: 13px;

  font-size: 14px;
  line-height: 14px;
  color: @gray-color;
}

.catalog-second__right-cell {
  text-align: right;
}

.catalog-second__button {
  width: @wrapper-width;
  height: 40px;

  margin: auto;
}

//------------------------------------------- TABLET -------------------------

@media (min-width: @tablet-width) {
  .catalog-second {
    min-width: @tablet-width;

    position: relative;

    &::before {
      .pseudo();
      width: 100%;
      height: 3px;

      top: 25px;
      right: 0;

      background-color: @leaf-color;
    }
  }

  .catalog-second__container {
    .flex-column(flex-start, center);
    width: @tablet-wrapper-width;
    min-height: 700px;

    margin: 0 auto;

    &::before {
      display: none;
    }
  }

  .catalog-second__header {
    width: 410px;

    margin-top: 2px;
    margin-bottom: 80px;
    margin-left: 0;
    padding-left: 38px;
    padding-right: 20px;

    font-size: 36px;
    line-height: 36px;

    &::after {
      display: none;
    }
  }

  .catalog-second__item {
    .flex-row(space-between, stretch);
    flex-wrap: wrap;
    .outline-no-top(@dim-color);
    width: @tablet-wrapper-width;

    padding-top: 28px;
    padding-bottom: 27px;
    padding-left: 38px;
    padding-right: 38px;

    &:first-child::after {
      .dim-line(0, 100%, @dim-color);
      top: 0;
    }

    &:not(:first-child)::after {
      display: none;
    }
  }

  .catalog-second__caption {
    width: 50%;

    margin-top: 0;
    margin-bottom: auto;
    margin-right: 10px;

    font-size: 20px;
    line-height: 20px;
  }

  .catalog-second__table {
    width: 50%;

    margin-top: 2px;
    margin-bottom: auto;

    font-size: 16px;
    line-height: 20px;
  }

  .catalog-second__left-cell {
    padding-right: 5px;

    text-align: right;
  }

  .catalog-second__right-cell {
    width: 90px;

    padding-left: 5px;
  }

  .catalog-second__button {
    width: 100%;
    margin-top: 22px;
  }
}

//------------------------------------------- DESKTOP -------------------------

@media (min-width: @desktop-width) {
  .catalog-second {
    min-width: @desktop-width;

    &::before {
      top: 30px;
      right: 0;
    }
  }

  .catalog-second__container {
    width: @desktop-wrapper-width;
    min-height: 500px;

    padding-bottom: 20px;
  }

  .catalog-second__header {
    width: 414px;

    margin-top: 7px;
    margin-left: -40px;
    padding-left: 40px;
    padding-right: 22px;
  }

  .catalog-second__wrapper {
    .flex-row(space-between, flex-start);
    width: @desktop-wrapper-width;
  }

  .catalog-second__item {
    flex-wrap: nowrap;
    box-shadow: none;
    width: 895px;
    min-height: 72px;

    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 0;
    padding-right: 0;

    &:first-child::before {
      .dim-line(0, 100%, @dim-color);
      bottom: 0;
    }

    &:not(:first-child)::after {
      display: block;
      .dim-line(0, 100%, @dim-color);
      bottom: 0;
    }
  }

  .catalog-second__link {
    flex-grow: 1;
    flex-basis: 200px;

    margin-top: auto;
    margin-bottom: auto;

    padding-right: 20px;

    align-self: center;
  }

  .catalog-second__table {
    flex-basis: auto;
    flex-grow: 0;

    width: 303px;

    margin: auto 0;
  }

  .catalog-second__row {
    .flex-row(flex-start, center);
    margin-top: 7px;
  }

  .catalog-second__left-cell {
    width: 240px;

    text-align: start;
  }

  .catalog-second__right-cell {
    width: 65px;

    text-align: start;
  }

  .catalog-second__button {
    width: 245px;

    margin-top: auto;
    margin-bottom: auto;
    margin-left: 20px;
  }
}
