.about-list {
  min-width: @mobile-width;
}

.about-list__container {
  .flex-column(flex-start, flex-start);
  width: @mobile-width;
  min-height: 447px;

  margin: 0 auto;
}

.about-list__header {
  .base-font();
  margin-top: 20px;
  margin-bottom: 0;
  margin-left: 20px;

  font-size: 36px;
  color: #111111;
}

.about-list__list {
  margin-top: 33px;
  margin-bottom: 10px;
  padding-left: 100px;

  list-style-type: none;
}

.about-list__item {
  .second-font();
  width: 195px;

  position: relative;

  margin-bottom: 27px;

  color: @gray-color;
  font-size: 14px;
  line-height: 18px;

  &_nutrition::after {
    .about-list-pseudo-position(-2px, -80px, 60px);
    background-image: url("../img/advantage-leaf@1x.png");

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/advantage-leaf@2x.png");
      background-size: 50px auto;
    }
  }

  &_powder::after {
    .about-list-pseudo-position(-2px, -80px, 60px);
    background-image: url("../img/advantage-powder@1x.png");

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/advantage-powder@2x.png");
      background-size: 50px auto;
    }
  }

  &_change::after {
    .about-list-pseudo-position(-2px, -80px, 60px);
    background-image: url("../img/advantage-dining@1x.png");

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/advantage-dining@2x.png");
      background-size: 50px auto;
    }
  }

  &_enjoy::after {
    .about-list-pseudo-position(-2px, -80px, 60px);
    background-image: url("../img/advantage-clock@1x.png");

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/advantage-clock@2x.png");
      background-size: 50px auto;
    }
  }

  & br {
    display: none;
  }
}

//------------------------------------------- TABLET -------------------------

@media (min-width: @tablet-width) {
  .about-list {
    min-width: @tablet-width;
    margin: 0 auto;
  }

  .about-list__container {
    position: relative;

    width: @tablet-wrapper-width;
    min-height: 780px;

    &::after {
      .dim-line(calc(@tablet-wrapper-width/2 - @tablet-width/2), @tablet-width, @dim-color);
      bottom: 0;
    }
  }

  .about-list__header {
    margin-top: 30px;
    margin-left: 0;

    font-size: 60px;
  }

  .about-list__list {
    .flex-row(flex-start, flex-start);
    flex-wrap: wrap;
    max-width: 100%;

    margin-top: 21px;
    margin-bottom: 5px;
    padding-left: 0;

    counter-reset: item;
  }

  .about-list__item {
    width: 245px;
    min-height: 230px;

    margin-top: 30px;
    margin-bottom: 57px;
    margin-right: 138px;
    padding-top: 117px;

    font-size: 16px;
    line-height: 24px;

    &::before {
      content: counter(item);
      counter-increment: item;

      position: absolute;
      top: 95px;
      left: 115px;

      .base-font();
      font-size: 281px;
      color: @lighten-color;

      z-index: -1;
    }

    &:nth-child(2n) {
      margin-right: 0;
    }

    &_nutrition::after {
      .about-list-pseudo-position(5px, 0, 80px);
    }

    &_powder::after {
      .about-list-pseudo-position(5px, 0, 80px);
    }

    &_change::after {
      .about-list-pseudo-position(5px, 0, 80px);
    }

    &_enjoy::after {
      .about-list-pseudo-position(5px, 0, 80px);
    }

    & br {
      display: block;
    }
  }
}

//------------------------------------------- DESKTOP -------------------------

@media (min-width: @desktop-width) {
  .about-list {
    min-width: @desktop-width;
    margin: 0;
  }

  .about-list__container {
    width: @desktop-wrapper-width;
    min-height: 280px;

    &::after {
      display: none;
    }
  }

  .about-list__item {
    margin-bottom: 27px;
    margin-right: 80px;

    &:nth-child(2n) {
      margin-right: 80px;
    }

    &:nth-child(4n) {
      margin-right: 0;
    }
  }
}
