.outline(@color) {
  box-shadow: inset 0 0 0 1px @color;
}

.outline-no-top(@color) {
  box-shadow: inset 0 -1px 0 0 @color,
    inset -1px 0 0 0 @color,
    inset 1px 0 0 0 @color;
}

.base-font(@weight: 400) {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: @weight;
}

.second-font(@weight: 400) {
  font-family: "Arial", sans-serif;
  font-weight: @weight;
}

.flex-column(@main-axis, @second-axis) {
  display: flex;
  flex-direction: column;
  justify-content: @main-axis;
  align-items: @second-axis;
}

.flex-row(@main-axis, @second-axis) {
  display: flex;
  flex-direction: row;
  justify-content: @main-axis;
  align-items: @second-axis;
}

.list-reset() {
  list-style: none;
  margin: 0;
  padding: 0;
}

.pseudo() {
  content: "";
  position: absolute;
}

.about-list-pseudo-position(@position-x, @position-y, @size) {
  .pseudo();
  top: @position-x;
  left: @position-y;

  width: @size;
  height: @size;

  background-position: center;
  background-repeat: no-repeat;
  background-color: @leaf-color;
}

.dim-line(@left: 0, @width: @mobile-width, @back-color: @dim-color) {
  .pseudo();
  left: @left;

  width: @width;
  height: 1px;

  background-color: @back-color;
}

.visually-hidden-mixin() {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
