.front-screen {
  .base-font();
  min-width: @mobile-width;
  min-height: 504px;

  position: relative;

  color: @light-color;

  &::before {
    .pseudo();
    width: 100%;
    height: 305px;

    top: 0;
    left: 0;
    z-index: -1;

    background-color: @leaf-color;
  }
}

.front-screen__container {
  .flex-column (flex-start, center);
  position: relative;

  width: @mobile-width;

  margin-top: 0;
  margin-bottom: 22px;
  margin-right: auto;
  margin-left: auto;

  &::before {
    .pseudo();
    width: @mobile-width;
    height: 305px;

    top: 0;
    left: 0;
    z-index: -1;

    background-image: url("../img/index-background-mobile@1x.jpg");
    background-position: top center;
    background-repeat: no-repeat;

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/index-background-mobile@2x.jpg");
      background-size: @mobile-width auto;
    }
  }
}

.front-screen__header {
  max-width: 100%;

  margin-top: 29px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;

  font-size: 36px;
  font-weight: 400;
  text-align: center;
  line-height: 36px;
}

.front-screen__button-caption {
  font-size: 14px;
  text-transform: uppercase;
  color: @light-color;
}

.front-screen__caption {
  margin: auto;

  font-size: 14px;
  text-transform: uppercase;
  color: @light-color;
}

.front-screen__img {
  width: 280px;
  height: 270px;

  margin-top: 26px;
  margin-bottom: 0;
}

.front-screen__button {
  width: @wrapper-width;
  height: 40px;

  margin: auto;
}

//------------------------------------------- TABLET -------------------------

@media (min-width: @tablet-width) {
  .front-screen {
    min-width: @tablet-width;
    min-height: 833px;

    color: @dark-color;

    &::before {
      display: none;
    }
  }

  .front-screen__container {
    .flex-column (flex-start, flex-start);
    width: @tablet-wrapper-width;
    position: relative;

    margin-top: 0;
    margin-bottom: 20px;
    margin-right: auto;
    margin-left: auto;
    padding-left: 87px;

    &::before {
      display: none;
    }
  }

  .front-screen__header {
    width: 420px;

    margin-top: 23px;
    margin-bottom: 37px;
    margin-left: 0;
    margin-right: auto;


    text-align: left;
    font-size: 60px;
    line-height: 60px;
  }

  .front-screen__caption {
    margin-left: 2px;
    margin-bottom: 40px;

    font-size: 20px;
    color: @dark-color;
  }

  .front-screen__button-caption {
    color: @light-color;

    font-size: 20px;
  }

  .front-screen__img {
    position: absolute;
    top: 222px;
    left: 0;

    width: 709px;
    height: 609px;

    margin-top: 0;
    margin-bottom: 0;

    z-index: -1;
  }

  .front-screen__button {
    width: 245px;
    height: 52px;

    margin-left: 0;
  }
}

//------------------------------------------- DESKTOP -------------------------

@media (min-width: @desktop-width) {
  .front-screen {
    min-width: @desktop-small-width;
    max-width: @desktop-small-width;
    min-height: 300px;

    position: absolute;
    top: 198px;
    right: 50%;
  }

  .front-screen__container {
    position: static;
    width: @desktop-small-width;
    padding-left: 80px;
  }

  .front-screen__caption {
    margin-bottom: 48px;
  }

  .front-screen__img {
    top: -56px;
    right: -391px;
    left: auto;

    width: 552px;
    height: 499px;

    z-index: 0;
  }
}
