.footer {
  .flex-column(flex-start, center);
  min-width: @mobile-width;
}

.footer__text-container {
  .flex-row(space-between, center);
  width: 100%;
  min-height: 114px;

  margin: 0 auto;

  background-color: @light-color;

  &_gray {
    background-color: @lighten-color;
  }
}

.footer__text {
  .base-font();
  width: 123px;

  margin-left: auto;
  margin-right: auto;

  transform: translateX(3px);

  font-size: 16px;
  text-transform: uppercase;
  color: @dark-color;
  line-height: 20px;
}

.footer__address {
  .second-font();
  width: 136px;

  margin-top: 5px;
  margin-right: auto;
  margin-left: auto;

  transform: translateX(-10px);

  font-size: 14px;
  color: @gray-color;
  line-height: 20px;
}

.footer__address p {
  margin-top: 0;
  margin-bottom: 0;
}

.footer__map {
  .flex-row(center, center);
  position: relative;

  width: 100%;
  height: 364px;

  background-image: url("../img/bg-map-mobile@1x.png");
  background-position: 50% -39px;
  background-repeat: no-repeat;
  background-size: @mobile-width auto;
  background-color: @lighten-color;

  z-index: -2;

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-map-mobile@2x.png");
  }

  &::after {
    .pseudo();
    top: calc(50% - 71px);
    left: calc(50% - 61px);

    width: 124px;
    height: 106px;

    transform: scale(0.5);

    z-index: -1;

    background-image: url("../img/map-pin.png");
  }
}

.footer__bottom-container {
  .flex-column(flex-start, center);
  width: 100%;

  background-color: @lighten-color;
}

.footer__logo-svg {
  fill: @dark-color;
}

.footer__logo {
  .flex-row(center, center);
  width: 100%;
  min-height: 93px;

  position: relative;

  &::after {
    .dim-line(calc(50% - @line), @wrapper-width);
    bottom: 0;
  }
}

.footer__logo-link {
  outline: none;

  // --- для IE ---
  margin-top: 32px;
  margin-bottom: 32px;
  // ---

  &:hover .footer__logo-svg,
  &:focus .footer__logo-svg {
    fill-opacity: 0.8;
  }

  &:active .footer__logo-svg {
    fill-opacity: 0.6;
  }
}

.footer__social {
  width: 100%;
  min-height: 63px;

  position: relative;

  // --- для IE ---
  padding-top: 22px;
  padding-bottom: 22px;
  // ---

  &::after {
    .dim-line(calc(50% - @line), @wrapper-width);
    bottom: 0;
  }
}

.footer__social-vk {
  margin-right: 36px;
}

.footer__social-fb {
  margin-left: 40px;
}

.footer__copyright {
  .second-font();
  .flex-row(space-between, center);
  width: @mobile-width;
  min-height: 77px;

  padding-left: 20px;
  padding-right: 20px;
  // --- для IE ---
  padding-top: 22px;
  padding-bottom: 22px;
  // ---

  outline: none;

  &:hover .footer__svg,
  &:focus .footer__svg {
    fill: @leaf-color;
  }

  &:active {
    opacity: 0.3;
  }
}

.footer__copyright span {
  font-size: 16px;
  line-height: 16px;
  color: @gray-color;
}

.footer__svg {
  fill: @brownish-grey-color;
}

//------------------------------------------- TABLET -------------------------

@media (min-width: @tablet-width) {
  .footer {
    min-width: @tablet-width;
  }

  .footer__text-container {
    min-height: 202px;

    padding-top: 7px;
    padding-left: 71px;
    padding-right: 71px;
  }

  .footer__bottom-container {
    .flex-row(center, center);
    min-height: 150px;
    width: 100%;

    // --- для IE ---
    padding-top: 29px;
    padding-bottom: 29px;
    // ---
  }

  .footer__logo {
    .flex-row(flex-start, center);
    flex-grow: 1;
    width: 33%;
    min-height: 24px;

    margin-left: 30px;
    margin-right: 20px;


    background-position: left center;

    &::after {
      display: none;
    }
  }

  .footer__social {
    width: 33%;

    &::after {
      display: none;
    }
  }

  .footer__copyright {
    .flex-row(flex-end, center);
    flex-grow: 1;
    width: 33%;

    margin-left: 33px;
    margin-right: 30px;
    padding-left: 0;
    padding-right: 0;
  }

  .footer__copyright-caption {
    margin-right: 23px;
  }

  .footer__social-vk {
    margin-right: 28px;
  }

  .footer__social-fb {
    margin-left: 29px;
  }

  .footer__text {
    min-width: 154px;

    margin-left: auto;
    margin-right: auto;

    transform: translateX(-80px);

    font-size: 20px;
    line-height: 25px;
  }

  .footer__address {
    width: 155px;

    margin-top: 0;
    margin-left: auto;

    transform: translateX(80px);

    font-size: 16px;
  }

  .footer__address p {
    margin-top: 20px;
    margin-bottom: 20px;

    text-align: end;
  }

  .footer__map {
    min-height: 396px;

    background-image: url("../img/bg-map-tablet@1x.png");
    background-size: @tablet-width auto;
    background-position: 50% -5px;

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-map-tablet@2x.png");
    }

    &::after {
      top: calc(50% - 51px);

      transform: none;
    }
  }

  .footer__copyright span {
    margin-right: 20px;
  }
}

//------------------------------------------- DESKTOP -------------------------

@media (min-width: @desktop-width) {
  .footer {
    min-width: @desktop-width;

    position: relative;
  }

  .footer__map {
    width: 100%;
    height: 400px;

    background-image: url("../img/bg-map-desktop@1x.png");
    background-position: 50% 0;
    background-size: 1440px 400px;
    background-repeat: no-repeat;

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-map-desktop@2x.png");
    }

    &::after {
      transform: translateX(340px);
    }
  }

  .footer__text-container {
    .flex-row(space-between, flex-start);
    position: absolute;
    top: 95px;
    right: calc(50% + 40px);

    width: 570px;
    min-height: 200px;

    background-color: @light-color;

    z-index: 1;

    padding-top: 45px;
    padding-left: 76px;
    padding-right: 90px;
  }

  .footer__text {
    margin-left: 0;

    transform: none;
  }

  .footer__address {
    margin-right: 0;

    transform: none;
  }

  .footer__address p {
    text-align: start;
  }

  .footer__logo {
    max-width: calc(@bottom);

    margin-left: 0;
    margin-right: 0;
  }

  .footer__social {
    max-width: calc(@bottom);

    transform: translate(-7px, -5px);
  }

  .footer__social-vk {
    margin-right: 28px;
  }

  .footer__social-fb {
    margin-left: 29px;
  }

  .footer__copyright {
    max-width: calc(@bottom);
    margin-left: 0;
    margin-right: 0;
  }
}
