.catalog-items {
  min-width: @mobile-width;
}

.catalog-items__container {
  .flex-column(flex-start, center);
  width: @mobile-width;

  margin-top: 0;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
}

.catalog-items__header {
  align-self: start;

  .base-font();
  margin-top: 15px;
  margin-bottom: 29px;
  margin-left: 20px;

  font-size: 36px;
  color: @dark-color;
  line-height: 60px;
}

.catalog-items__list {
  .list-reset();
  .flex-column(flex-start, center);

  width: @mobile-width;
}

.catalog-items__item {
  .outline-no-top(@dim-color);
  width: @mobile-width;
  min-height: 209px;

  position: relative;

  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;

  &:first-child {
    .outline(@dim-color);
  }
}

.catalog-items__wrapper {
  .flex-row(flex-start, flex-start);
}

.catalog-items__button {
  width: @wrapper-width;
  height: 40px;

  margin: auto;
}

.catalog-items__link {
  font-size: 0;
  .flex-column(center, center);
}

.catalog-items__img {
  margin-top: 1px;
  width: 140px;
  height: 144px;
}

.catalog-items__table-wrapper {
  .flex-column(flex-start, flex-start);
}

.catalog-items__caption {
  .base-font();
  width: 80px;

  margin-top: 22px;
  margin-bottom: 13px;

  font-size: 16px;
  line-height: 20px;
  color: @dark-color;
  text-transform: uppercase;

  &_plus {
    width: @mobile-width - 40;

    position: relative;

    margin-top: 80px;
    margin-bottom: 5px;
    margin-left: auto;
    margin-right: auto;

    text-align: center;
  }

  &_plus::before {
    .pseudo();
    top: -40px;
    left: calc(50% - 16px);

    width: 31px;
    height: 3px;

    background-color: @smooth-color;
  }

  &_plus::after {
    .pseudo();
    top: -54px;
    left: calc(50% - 2px);

    width: 3px;
    height: 31px;

    background-color: @smooth-color;
  }
}

.catalog-items__table {
  .second-font();
  width: 140px;

  font-size: 12px;
  line-height: 16px;
  color: @gray-color;
}

.catalog-items__right-cell {
  text-align: end;
}

.catalog-items__comment {
  .second-font();
  margin-top: 0;
  margin-bottom: 19px;

  text-align: center;

  font-size: 12px;
  line-height: 20px;
  color: @gray-color;
}

//------------------------------------------- TABLET -------------------------

@media (min-width: @tablet-width) {
  .catalog-items {
    min-width: @tablet-width;
  }

  .catalog-items__container {
    width: @tablet-width;
  }

  .catalog-items__header {
    margin-top: 22px;
    margin-bottom: 90px;
    margin-left: 30px;

    font-size: 60px;
  }

  .catalog-items__list {
    .flex-row(flex-start, center);
    flex-wrap: wrap;

    width: @tablet-wrapper-width;
  }

  .catalog-items__item {
    width: 324px;
    min-height: 431px;

    margin-top: 75px;
    margin-bottom: 65px;
    margin-left: 0;
    margin-right: 60px;

    background-color: @lighten-color;

    box-shadow: none;

    &_last-one {
      .outline(@dim-color);
      .flex-column(flex-start, center);
      background-color: @light-color;
    }

    &:nth-child(2n) {
      margin-right: 0;
    }

    &:first-child {
      box-shadow: none;
    }
  }

  .catalog-items__wrapper {
    .flex-column(flex-start, center);

    margin-bottom: 35px;
  }

  .catalog-items__img {
    position: absolute;
    bottom: 0;
    left: 0;

    width: 324px;
    height: auto;

    transform: translateY(-305px);

    margin-top: 0;
  }

  .catalog-items__table-wrapper {
    .flex-column(flex-start, center);
  }

  .catalog-items__caption {
    width: 135px;

    margin-top: 164px;
    margin-bottom: 20px;

    font-size: 20px;

    text-align: center;

    &_plus::before {
      top: -80px;
      left: calc(50% - 30px);

      width: 61px;
      height: 2px;
    }

    &_plus::after {
      top: -110px;
      left: calc(50% - 1px);

      width: 2px;
      height: 61px;
    }
  }

  .catalog-items__table {
    width: @tablet-item-width;

    font-size: 16px;
  }

  .catalog-items__comment {
    margin-top: 5px;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;

    width: 167px;
    min-height: 85px;

    font-size: 16px;
  }

  .catalog-items__button {
    width: @tablet-item-width;
  }

  .catalog-items__row {
    height: 30px;

    border-bottom: 1px solid @dim-color;
  }
}

//------------------------------------------- DESKTOP -------------------------

@media (min-width: @desktop-width) {
  .catalog-items {
    min-width: @desktop-width;
  }

  .catalog-items__container {
    width: @desktop-wrapper-width;
  }

  .catalog-items__header {
    margin-top: -10px;
    margin-left: 0;
  }

  .catalog-items__list {
    width: @desktop-wrapper-width;
  }

  .catalog-items__item {
    width: @desktop-item-width;

    margin-bottom: 67px;
    margin-right: 80px;

    &:nth-child(2n) {
      margin-right: 80px;
    }

    &:nth-child(4n) {
      margin-right: 0;
    }
  }

  .catalog-items__wrapper {
    margin-bottom: 34px;
  }

  .catalog-items__table {
    width: @desktop-button-width;
  }

  .catalog-items__button {
    width: @desktop-button-width;
  }

  .catalog-items__caption {
    margin-top: 162px;
  }

  .catalog-items__row {
    height: 31px;
  }
}
